.modal-enter {
    opacity: 0;
  transform: scale(0.9);
}

.modal-enter-active {
    opacity: 1;
    transform: translateX(0);
    transition: opacity 500ms, transform 500ms;
}

.modal-exit {
    opacity: 1;
}

.modal-exit-active {
    opacity: 0;
  transform: scale(0.9);
  transition: opacity 300ms, transform 300ms;
}